.new-lead-card {
  display: flex;
  border: 1px solid #aaa;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 3px 3px 10px #aaa;
}

.new-lead-form {
  width: 100%;
}

.new-lead-form-field {
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
}

.new-lead-form-field label {
  margin-bottom: 0.5rem;
}

.new-lead-form-field input {
  padding: 0.5rem;
  border: 1px solid #888;
  border-radius: 0.4rem;
}

.new-lead-form-field input:hover {
  border-color: #2196f3;
}

.new-lead-form-field input:focus {
  border: 2px solid #2196f3;
  outline: none;
}

.lead-phone {
  width: 100%;
}

.new-lead-submit {
  border: 0;
  border-radius: 0.5rem;
  padding: 0.75rem;
  font-weight: bold;
  color: white;
  background-color: #2196f3;
  margin-top: 1rem;
}

.new-lead-submit:hover {
  background-color: #42a5f5;
  cursor: pointer;
}

.new-lead-submit:disabled {
  background-color: #90caf9;
  cursor: auto;
}

.new-lead-submit:focus {
  border: 2px solid #1565c0;
  outline: none;
}
